



































































import { Component, Vue } from 'vue-property-decorator';
import { SymbolTicker } from '@/types';
import { Getter } from 'vuex-class';
@Component
export default class HotTicker extends Vue {
  @Getter('allTicker', { namespace: 'ticker' }) allTicker!: SymbolTicker[];
  @Getter('rateUSD', { namespace: 'ticker' }) rateUSD!: number;
}
