/**
 * Number formatting
 * like 10000 => 10k
 * @param {number} num
 * @param {number} digits
 */
export function numberFormatter (num: number, digits: number | undefined) {
  const si = [
    { value: 1E18, symbol: 'E' },
    { value: 1E15, symbol: 'P' },
    { value: 1E12, symbol: 'T' },
    { value: 1E9, symbol: 'G' },
    { value: 1E6, symbol: 'M' },
    { value: 1E3, symbol: 'k' }];
  for (let i = 0; i < si.length; i++) {
    if (num >= si[i].value) {
      return (num / si[i].value).toFixed(digits).replace(/\.0+$|(\.[0-9]*[1-9])0+$/, '$1') + si[i].symbol;
    }
  }
  return num.toString();
}
/**
 * 10000 => "10,000"
 * @param {number} num
 */
export function toThousandFilter (num: string | number) {
  const negative = num < 0;
  negative && (num = -num);
  const digits = -Math.floor(Math.log10((+num || 0)) + 1);
  const number = num > 1000 ? (+num || 0).toFixed(1) : (num > 100 ? (+num || 0).toFixed(2) : (num > 0.1 ? (+num || 0).toFixed(4) : (num > 0 ? (+num || 0).toFixed(digits + 5) : 0)));
  return (negative ? '- ' : '') + number.toString().replace(/^-?\d+/g, m => m.replace(/(?=(?!\b)(\d{3})+$)/g, ','));
}
/**
 * Upper case first char
 * @param {String} string
 */
export function uppercaseFirst (string: string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}
