































































































































































import { Component, Vue, Watch } from 'vue-property-decorator';
import HotTicker from '../components/HotTicker.vue';
import ListTicker from '../components/ListHomeTicker.vue';
import { Getter, namespace } from 'vuex-class';
import { SymbolTicker, UserInfo } from '@/types';
import UserService from '@/services/userService';
import store from '@/store';
import { mdiApi, mdiAndroid, mdiCreditCardOutline } from '@mdi/js';
import TickerService from '@/services/tikerService';
import { TEST_MODE, API_URL } from '@/common/config';

const Auth = namespace('Auth');

@Component({
  components: {
    HotTicker,
    ListTicker
  }
})
export default class Home extends Vue {
  @Getter('allTicker', { namespace: 'ticker' }) allTicker!: SymbolTicker[];
  @Getter('tradingList', { namespace: 'ticker' }) tradingList!: any[];
  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;
  @Getter('tabHome', { namespace: 'topToolbar' }) tabHome!: string;
  @Auth.Action
  private getUserInfo!: () => Promise<any>;

  symbolSelected = '';
  isSearchLoading = false;
  addBalance = false;
  amount = 0;
  listTickerSearch = [];
  model = null;
  search = null;
  loading = true;
  reloadBalance = false;
  testMode = TEST_MODE;
  listUser: any[] = [];
  listTrading: any[] = [];
  icon = {
    api: mdiApi,
    android: mdiAndroid,
    payment: mdiCreditCardOutline
  };

  apiUrl = API_URL

  @Watch('allTicker')
  changeTicker (val: any) {
    this.getListTrading();
  }

  @Watch('search')
  async searchTicker (val: any) {
    // Items have already been loaded
    if (this.isSearchLoading) return;
    this.isSearchLoading = true;
    this.listTickerSearch = await TickerService.findTicker({ symbol: val }).then(response => {
      return response.data;
    });
    this.isSearchLoading = false;
  }

  async created () {
    await this.initPage();
    this.loading = false;
    !this.listTrading.length && await this.getListTrading();
  }

  async getTickerList () {
    await TickerService.list().then((res: any) => {
      if (res.success) {
        store.dispatch('ticker/setTradingList', res.data);
      }
    });
  }

  get tab () {
    return parseInt(this.tabHome);
  }

  set tab (val) {
    store.dispatch('topToolbar/changeTabHome', val);
  }

  async updateBalance () {
    this.reloadBalance = true;
    await UserService.updateBalance().then(async (res: any) => {
      if (res.success) {
        this.userData.balanceUSDT = res.data;
      }
    },
    () => {
      console.log('error');
    });
    this.reloadBalance = false;
  }

  async addFunding () {
    this.reloadBalance = true;
    await UserService.addFunding(this.amount, false).then(async (res: any) => {
      if (res.success) {
        this.userData.balanceUSDT = res.data;
        this.addBalance = false;
      }
    },
    () => {
      console.log('error');
    });
    this.reloadBalance = false;
  }

  async getListTrading () {
    let list: { price: any; trade: any; symbol: string; sort: number }[] = [];
    this.userData && this.userData.tickerListTrading.forEach((item: string) => {
      item = item.toUpperCase();
      let tickerPrice;
      let tickerTrade: {enable: boolean; tradeData: any} | undefined;
      if (this.allTicker.length) {
        tickerPrice = this.allTicker.find(x => x.s === item);
      }
      if (this.tradingList.length) {
        tickerTrade = this.tradingList.find((x: any) => x.symbol === item);
      }
      list.push({
        symbol: item,
        price: tickerPrice,
        trade: tickerTrade,
        sort: ((tickerTrade && tickerTrade.tradeData && tickerTrade.tradeData.length) ? 0 : (tickerTrade && tickerTrade.enable) ? 1 : 2)
      });
    });
    list = list.sort(function (a, b) {
      return a.sort - b.sort;
    });
    this.listTrading = list;
  }

  async reload (done: () => void) {
    await this.initPage(true);
    done();
  }

  infinite (done: () => void) {
    setTimeout(() => {
      done();
    }, 50000);
  }

  async initPage (reload = false) {
    // (!this.userData || reload) && await this.getUserInfo();
    /* (!this.tradingList.length || reload) && */await this.getTickerList();
  }
}
