


















import { Component, Vue } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import SocialLogin from '../components/SocialLogin.vue';
import { APP_MODE } from '@/common/config';
const Auth = namespace('Auth');

@Component({
  components: {
    SocialLogin
  }
})
export default class Login extends Vue {
  private user = {
    username: '',
    password: ''
  };

  private loading = false;
  private detailMsg = {
    type: '',
    msg: ''
  };

  disableGoogleLogin = APP_MODE;
  passwordRule = [
    (v: any) => !!v || 'Vui lòng nhập mật khẩu',
    (v: string | any[]) => (v && (v.length >= 8)) || 'Mật khẩu phải lớn hơn 8 ký tự'
  ];

  confirmPasswordRule = [
    (v: any) => !!v || 'Vui lòng xác nhận mật khẩu',
    (v: string | any[]) => (v && (v === this.user.password)) || 'Mật khẩu không khớp'
  ];

  emailRule = [
    (v: any) => !!v || 'Vui lòng nhập email',
    (v: string) => (!v || (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v))) || 'Email chưa đúng'
  ];

  @Auth.Getter
  private isLoggedIn!: boolean;

  @Auth.Action
  private register!: (data: any) => Promise<any>;

  @Auth.Action
  private loginSocial!: (data: any) => Promise<any>;

  $validator: any;

  created () {
    if (this.isLoggedIn) {
      this.$router.push({ name: 'home' });
    }
  }

  handleLogin () {
    if (!(this.$refs.frmLogin as Vue & { validate: () => boolean }).validate()) return;
    this.loading = true;
    if (this.user.username && this.user.password) {
      this.register(this.user).then(
        () => {
          this.$router.push({ name: 'home' });
        },
        (error) => {
          this.loading = false;
          this.detailMsg = {
            type: 'error',
            msg: error
          };
        }
      );
    }
  }

  handleSocialLogin (userInfo: any) {
    this.loading = true;
    this.loginSocial({ idToken: userInfo.auth.id_token }).then(
      (data) => {
        this.$router.push({ name: 'home' });
      },
      (error) => {
        this.loading = false;
        this.detailMsg = {
          type: 'error',
          msg: error
        };
      }
    );
  }
}
