<template>
    <div v-if="list.length">
        <v-list class="pt-0">
        <template v-for="(item, index) in list">
        <v-divider :key="index"></v-divider>
        <v-list-item
                :key="index+'l'" class="px-2">
            <v-list-item-content class="py-2 list-trade" @click="$router.push(`/auto-trading/${item.symbol}`)">
                <div class="d-flex justify-space-between align-center">
                    <div class="subtitle-1 pb-1" v-if="item.trade && item.trade.enable">
                        <v-badge
                                v-bind:color="item.trade.pause ? 'red darken-4' : 'deep-orange'"
                                inline
                                v-bind:content="item.trade.pause ? 'Tạm dừng' : 'Giao dịch'"
                        >
                            <div class="d-flex align-center">
                                <v-list-item-avatar class="my-0"><v-img class="elevation-6" :src="`${apiUrl}/icon/${item.symbol.replace('USDT', '').toLowerCase()}.png`"></v-img></v-list-item-avatar>
                                <v-list-item-content class="pa-0">{{item.symbol.replace('USDT','/USDT')}}</v-list-item-content>
                            </div>
                        </v-badge></div>
                    <div v-else class="subtitle-1 pb-1">
                        <div class="d-flex align-center ">
                            <v-list-item-avatar class="my-0"><v-img class="elevation-6" :src="`${apiUrl}/icon/${item.symbol.replace('USDT', '').toLowerCase()}.png`"></v-img></v-list-item-avatar>
                            <v-list-item-content class="pa-0">{{item.symbol.replace('USDT','/USDT')}}</v-list-item-content>
                        </div>
                    </div>
                    <template v-if="item.trade && item.trade.tradeData.length && item.price">
                        <v-chip class="up" outlined color="teal accent-4" v-if="item.trade.tradeData[0].totalAmountTrade * parseFloat(item.price.c) >= (item.trade.tradeData[0].totalAmount + (item.trade.tradeData[0].profit ? item.trade.tradeData[0].profit : 0))">+{{((parseFloat(item.price.c) /(item.trade.tradeData[0].totalAmount / item.trade.tradeData[0].totalAmountTrade) - 1) * 100).toFixed(2)}}%</v-chip>
                        <v-chip class="down" outlined color="red darken-4" v-else>{{((parseFloat(item.price.c) /((item.trade.tradeData[0].totalAmount + (item.trade.tradeData[0].profit ? item.trade.tradeData[0].profit : 0)) / item.trade.tradeData[0].totalAmountTrade) - 1) * 100).toFixed(2)}}%</v-chip>
                    </template>
                    <template v-else>
                        <v-chip class="up" outlined color="secondary lighten-3">+0.00%</v-chip>
                    </template>
                </div>
                <v-list-item-subtitle>Số lệnh mua: <span class="font-weight-bold" v-if="item.trade && item.trade.tradeData.length">{{item.trade.tradeData[0].buyList.length - 1}}</span><span v-else>0</span></v-list-item-subtitle>

                <v-list-item class="d-flex justify-space-between mt-3 px-0 align-start">
                    <div class="left">
                        <v-list-item-subtitle>Số lượng</v-list-item-subtitle>
                        <v-list-item-title class="py-1"><span v-if="item.trade && item.trade.tradeData.length">{{item.trade.tradeData[0].totalAmountTrade | toThousandFilter}}</span><span v-else>0.0000</span></v-list-item-title>
                        <v-list-item-subtitle class="pt-2">Giá mua vào</v-list-item-subtitle>
                        <v-list-item-title class="py-1"><v-icon v-if="item.trade && !item.trade.tradeData.length" x-small>mdi-greater-than</v-icon><v-icon v-else x-small>mdi-less-than</v-icon> <span v-if="(item.trade && item.trade.priceUpBuy) || (item.trade && item.trade.tradeData.length && item.trade.tradeData[0].buyList.length - 1 < item.trade.numberOfOrder)">{{ item.trade && item.trade.tradeData.length ? ( (item.trade.tradeData[0].totalAmount + (item.trade.tradeData[0].profit ? item.trade.tradeData[0].profit : 0)) / item.trade.tradeData[0].totalAmountTrade) * (1 - (JSON.parse(item.trade.orderTimeRate)[item.trade.tradeData[0].buyList.length - 1] /100)) : item.trade.priceUpBuy * ((item.trade.callbackBuy / 100) + 1) | toThousandFilter}}</span><span v-else>0.0000</span></v-list-item-title>
                    </div>
                    <div class="right">
                        <v-list-item-subtitle>Giá hiện tại</v-list-item-subtitle>
                        <v-list-item-title class="py-1"><span v-if="item.price">{{item.price.c | toThousandFilter}}</span><span v-else>0.000</span></v-list-item-title>
                        <v-list-item-subtitle class="pt-2">Giá bán ra</v-list-item-subtitle>
                        <v-list-item-title class="py-1"><v-icon x-small>mdi-greater-than</v-icon> <span v-if="item.trade && item.trade.tradeData.length && item.trade.tradeData[0].buyList.length">{{ ((item.trade.tradeData[0].totalAmount + (item.trade.tradeData[0].profit ? item.trade.tradeData[0].profit : 0)) / item.trade.tradeData[0].totalAmountTrade) * ((item.trade.sellRate + item.trade.callbackSell) / 100 + 1) | toThousandFilter}}</span><span v-else>0.0000</span></v-list-item-title>
                    </div>
                    <div class="right">
                        <v-list-item-subtitle>Giá bình quân</v-list-item-subtitle>
                        <v-list-item-title class="py-1"><span v-if="item.trade && item.trade.tradeData.length && item.trade.tradeData[0].totalAmount">{{( (item.trade.tradeData[0].totalAmount + (item.trade.tradeData[0].profit ? item.trade.tradeData[0].profit : 0)) / item.trade.tradeData[0].totalAmountTrade) | toThousandFilter}}</span><span v-else>0.0000</span></v-list-item-title>
                    </div>
                </v-list-item>
            </v-list-item-content>
        </v-list-item>
        </template>
        </v-list>
    </div>
    <div v-else class="text-center py-5">
        <span class="caption">Chưa có cặp lệnh nào</span>
    </div>
</template>

<script>
import { API_URL } from '@/common/config';
export default {
  name: 'ListHomeTicker',
  props: ['list'],
  data () {
    return {
      apiUrl: API_URL
    };
  }
};
</script>
