









import { Component, Prop, Vue } from 'vue-property-decorator';
@Component
export default class SocialLogin extends Vue {
  @Prop(Boolean) readonly loading: boolean | undefined
  isInit = false
  $gAuth: any;
  isSignIn = false;
  loginWithGoogle () {
    this.$gAuth
      .signIn()
      .then(async (GoogleUser: { getAuthResponse: () => any; getBasicProfile: () => { (): any; new(): any; getName: { (): any; new(): any }; getEmail: { (): any; new(): any }; getImageUrl: { (): any; new(): any } } }) => {
        // on success do something
        const userInfo = {
          loginType: 'google',
          auth: GoogleUser.getAuthResponse(),
          user: {
            name: GoogleUser.getBasicProfile().getName(),
            email: GoogleUser.getBasicProfile().getEmail(),
            profileImage: GoogleUser.getBasicProfile().getImageUrl()
          }
        };
        this.$emit('social-login', userInfo);
      }).catch(() => {
        this.$emit('login', false);
      });
  }

  mounted () {
    const checkGauthLoad = setInterval(() => {
      this.isInit = this.$gAuth.isInit;
      this.isSignIn = this.$gAuth.isAuthorized;
      if (this.isInit) clearInterval(checkGauthLoad);
    }, 1000);
  }
}
