import axios from 'axios';
import authHeader from './authHeader';
import { API_URL } from '@/common/config';
class UserService {
  getPublicContent () {
    return axios.get(API_URL + 'all');
  }

  fetch (params: any) {
    return axios
      .get(API_URL + '/user', {
        headers: authHeader(),
        params: params
      })
      .then(response => {
        return response.data;
      });
  }

  apiConnect (apiConfig: any) {
    return axios
      .post(API_URL + '/connect-api', apiConfig, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  changeTickerList (tickerList: any) {
    return axios
      .post(API_URL + '/user/update-ticker', { tickerList: tickerList }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  getUserInfo () {
    return axios
      .get(API_URL + '/user-info', { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  updateBalance () {
    return axios
      .get(API_URL + '/user-balance', { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  addFunding (amount: number, addBalance: boolean) {
    return axios
      .post(API_URL + '/set-balance', { amount: amount, addBalance: addBalance }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  changeAutoResume (autoResume: boolean) {
    return axios
      .post(API_URL + '/user/change-auto-resume', { autoResume: autoResume }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  changeGuaranteedProfit (guaranteedProfit: boolean) {
    return axios
      .post(API_URL + '/user/change-guaranteed-profit', { guaranteedProfit: guaranteedProfit }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  changeConfig (params: any) {
    return axios
      .post(API_URL + '/user/change-config', params, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  pauseBot (pause: boolean) {
    return axios
      .post(API_URL + '/user/pause-bot', { pause: pause }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }
}

export default new UserService();
