




















import { Component, Vue, Watch } from 'vue-property-decorator';
import TopToolbar from './components/TopToolbar.vue';
import BottomNav from './components/BottomNav.vue';
import store from '@/store';
import { Getter, namespace } from 'vuex-class';
import { SymbolTicker, UserInfo } from '@/types';
import axios from 'axios';
import PullToRefresh from 'pulltorefreshjs';
import { APP_MODE, TEST_MODE } from '@/common/config';
const Auth = namespace('Auth');
@Component({
  components: {
    TopToolbar,
    BottomNav
  }
})
export default class extends Vue {
  @Getter('allTicker', { namespace: 'ticker' }) allTicker!: SymbolTicker[];
  @Getter('tradingList', { namespace: 'ticker' }) tradingList!: any;
  @Getter('rateUSD', { namespace: 'ticker' }) rateUSD!: number;
  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;

  @Watch('$route')
  async onUrlChange (to: any) {
    this.symbol = to.params.symbol ?? '';
    this.hideNav = !['home', 'message', 'account'].includes(to.name);
    // @ts-ignore
    this.routerTransition = this.$router.isBack ? 'slide-right' : 'slide-left';
    // @ts-ignore
    this.$router.isBack = false;
    if (!['login', 'register'].includes(to.name ?? '')) {
      await this.initPage();
      document.addEventListener('visibilitychange', this.handleVisibilityChange, false);
    }
  }

  @Watch('symbolList')
  async changeList (val: any) {
    if (!val.length) {
      try {
        this.binanceSocket.close();
      } catch (e) {}
    } else {
      (JSON.stringify(this.listTicker) !== JSON.stringify(val)) && this.getTickerLastPrice();
      val.length > 1 && (this.listTicker = val);
      this.connectSocket();
    }
  }

  symbol = ''
  listTicker: any = [];
  hideNav = true;
  testMode = TEST_MODE;
  connectedSocket = false
  routerTransition = 'slide-right';
  binanceSocket: any = {};

  @Auth.Action
  private getUserInfo!: () => Promise<any>;

  async created () {
    const route = this.$route.name ?? '';
    this.hideNav = !['home', 'message', 'account'].includes(route);
    if (!['login', 'register'].includes(route ?? '')) {
      this.symbol = this.$route.params.symbol ?? '';
      await this.initPage();
      document.addEventListener('visibilitychange', this.handleVisibilityChange, false);
    }
  }

  mounted () {
    APP_MODE && PullToRefresh.init({
      mainElement: '#content',
      onRefresh () {
        window.location.reload();
      },
      refreshTimeout: 100
    });
  }

  get symbolList () {
    if (!this.userData && !this.symbol) return [];
    if (!this.symbol && this.userData && this.$route.name === 'home' && this.tradingList) {
      const listTrading = this.tradingList.map((item: any) => { return item.symbol; });
      const listDefault = this.userData.tickerList.map((item: string) => {
        return item.toUpperCase();
      });
      const list = listDefault.filter((x: string) => !listTrading.includes(x));
      this.userData.tickerListTrading = listTrading.concat(list.slice(0, listDefault.length - listTrading.length));
      return this.userData.tickerListTrading;
    } else if (this.symbol) {
      return [this.symbol];
    } else {
      return [];
    }
  }

  async initPage () {
    !this.userData && await this.getUserInfo();
    !this.rateUSD && await this.getRateUSD();
  }

  connectSocket () {
    try {
      this.binanceSocket.close();
    } catch (e) {}
    this.binanceSocket = new WebSocket('wss://stream.binance.com:9443/stream');
    this.binanceSocket.onopen = () => {
      this.connectedSocket = true;
      const listTickerMini = this.symbolList.map((item: string) => {
        return item.toLocaleLowerCase() + '@miniTicker';
      });
      this.binanceSocket.send(`{"method": "SUBSCRIBE","params":${JSON.stringify(listTickerMini)},"id": 1}`);
    };
    this.binanceSocket.onmessage = (event: { data: string }) => {
      const ticker = JSON.parse(event.data).data;
      if (!ticker) return;
      store.dispatch('ticker/updateTicker', ticker);
    };
  }

  getTickerLastPrice () {
    if (this.allTicker.length > 1 && this.symbolList.length === 1 && this.allTicker.find((x: any) => x.s === this.symbolList[0])) {
      return;
    }
    return axios.get('https://api.binance.com/api/v3/ticker/price', { params: { symbols: JSON.stringify(this.symbolList) } }).then(response => {
      const listPrice = response.data;
      const listTickerPrice = listPrice.map((item: any) => {
        return {
          c: item.price, // Close price
          s: item.symbol // Symbol
        };
      });
      if (listTickerPrice.length === 1 && this.allTicker.length) {
        store.dispatch('ticker/updateTicker', listTickerPrice[0]);
      } else {
        store.dispatch('ticker/changeAllTicker', listTickerPrice);
      }
    });
  }

  getRateUSD () {
    return axios.get('https://min-api.cryptocompare.com/data/price?fsym=USD&tsyms=USDT').then(response => {
      store.dispatch('ticker/changeRateUSD', response.data ? response.data.USDT : 0);
    });
  }

  handleVisibilityChange () {
    if (document.hidden) {
      this.binanceSocket.close();
    } else {
      this.connectSocket();
    }
  }
}
