














































import { Component, Vue } from 'vue-property-decorator';
import UserService from '@/services/userService';
import { Getter, namespace } from 'vuex-class';
import { UserInfo } from '@/types';
const Auth = namespace('Auth');

@Component({
  components: {

  }
})
export default class ApiConnect extends Vue {
  private detailMsg = {
    type: '',
    msg: ''
  };

  loading = false
  apiConnected = false
  editApi = true
  apiKey = ''
  secret = ''

  apiKeyRule = [(v: number) => !!v || 'Vui lòng nhập Api Key']
  secretRule = [(v: number) => !!v || 'Vui lòng nhập Api Secret']

  @Auth.Action
  private getUserInfo!: () => Promise<any>;

  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;
  async created () {
    !this.userData && await this.getUserInfo();
    if (this.userData && this.userData.apiKey) {
      this.apiKey = this.userData.apiKey;
      this.secret = this.userData.secret;
      this.editApi = false;
    }
  }

  saveOrUpdate () {
    if (!(this.$refs.apiConnectFrm as Vue & { validate: () => boolean }).validate()) return;
    this.loading = true;
    UserService.apiConnect({ apiKey: this.apiKey, secret: this.secret, apiProvide: 'binance' }).then(
      () => {
        this.detailMsg = {
          type: 'success',
          msg: 'Đã kết nối với sàn Binance'
        };
        this.editApi = false;
        this.loading = false;
        this.getUserInfo();
      },
      (error) => {
        this.loading = false;
        this.detailMsg = {
          type: 'error',
          msg: (error.response && error.response.data && error.response.data.msg)
        };
      }
    );
  }
}
