




















































import { Component, Vue } from 'vue-property-decorator';
import TradeDataService from '@/services/tradeDataService';
import { Getter } from 'vuex-class';
import { mdiMenuRight } from '@mdi/js';
import TickerService from '@/services/tikerService';
import UserService from '@/services/userService';
@Component({
  components: {

  }
})
export default class Transaction extends Vue {
  @Getter('rateUSD', { namespace: 'ticker' }) rateUSD!: number;
  userList = [];
  userId = '';
  loading = true;
  Page = 1;
  PageLimit = 9999;
  icon = {
    more: mdiMenuRight
  };

  loadingOverlay = false;

  async created () {
    await this.getuserList();
    this.loading = false;
  }

  async getuserList () {
    await UserService.fetch({ Page: this.Page, PageLimit: this.PageLimit }).then((res: any) => {
      if (res.success) {
        this.userList = res.data;
      }
    });
  }

  syncFrom () {
    this.loadingOverlay = true;
    TickerService.copyConfig(this.userId).then(
      (data) => {
        this.loadingOverlay = false;
      }
    );
  }
}
