























































import { Component, Vue } from 'vue-property-decorator';
import TradeDataService from '@/services/tradeDataService';
import moment from 'moment';
import { Getter } from 'vuex-class';
import { mdiMenuRight } from '@mdi/js';
@Component({
  components: {

  }
})
export default class Transaction extends Vue {
  @Getter('rateUSD', { namespace: 'ticker' }) rateUSD!: number;
  tradingList = [];
  totalProfit = 0;
  todayProfit = 0;
  loading = true;
  icon = {
    more: mdiMenuRight
  };

  async created () {
    await this.getTradingList();
    this.loading = false;
  }

  async getTradingList () {
    await TradeDataService.fetch({ sell: true }).then((res: any) => {
      if (res.success) {
        this.tradingList = res.data;
      }
    });
  }

  get tradingDataTimeline () {
    const list: { profit: any; date: string; fullDate: string; isToday: boolean}[] = [];
    const listDate: string[] = [];
    let total = 0;
    if (this.tradingList.length) {
      this.tradingList.forEach((item: any) => {
        const date = moment(item.sellAt, 'YYYY-MM-DD').format('DD/MM/YYYY');
        total += item.profit ? item.profit : 0;
        if (!listDate.includes(date)) {
          listDate.push(date);
          const itemOnDate = this.tradingList.filter((x: any) => moment(x.sellAt, 'YYYY-MM-DD').format('DD/MM/YYYY') === date);
          const total = itemOnDate.reduce((previous, current: any) => { return previous + current.profit; }, 0);
          list.push({
            profit: total,
            date: moment(date, 'DD/MM/YYYY').format('DD/MM'),
            fullDate: moment(date, 'DD/MM/YYYY').format('YYYY-MM-DD'),
            isToday: date === moment().format('DD/MM/YYYY')
          });
        }
      });
      this.totalProfit = total;
    }
    return list;
  }
}
