import { ActionTree, Module, MutationTree, GetterTree } from 'vuex';
import { TickerState, RootState } from '@/types';
import Vue from 'vue';

const state: TickerState = {
  allTicker: [],
  tradingList: '',
  rateUSD: 0
};

export const getters: GetterTree<TickerState, RootState> = {
  allTicker: (theState: TickerState) => {
    return theState.allTicker;
  },
  tradingList: (theState: any) => {
    return theState.tradingList;
  },
  rateUSD: (theState: TickerState) => {
    return theState.rateUSD;
  }
};

const mutations: MutationTree<TickerState> = {
  setAllTicker (theState: TickerState, allTicker: any) {
    theState.allTicker = allTicker;
  },
  setTradingList (theState: TickerState, tradingList: any) {
    theState.tradingList = tradingList;
  },
  updateTicker (theState: TickerState, ticker: any) {
    // @ts-ignore
    const objIndex = theState.allTicker.findIndex((x: { s: any }) => x.s === ticker.s);
    if (objIndex !== -1) {
      // @ts-ignore
      Vue.set(theState.allTicker, objIndex, ticker);
    }
  },
  setRateUSD (theState: TickerState, rate: number) {
    theState.rateUSD = rate;
  }
};

export const actions: ActionTree<TickerState, RootState> = {
  changeAllTicker ({ commit }, allTicker: any) {
    commit('setAllTicker', allTicker);
  },
  setTradingList ({ commit }, tradingList: any) {
    commit('setTradingList', tradingList);
  },
  updateTicker ({ commit }, ticker: any) {
    commit('updateTicker', ticker);
  },
  changeRateUSD ({ commit }, rate: number) {
    commit('setRateUSD', rate);
  }
};

export const ticker: Module<TickerState, RootState> = {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
