import Vue from 'vue';
import Router from 'vue-router';
import store from '../store';

import Home from '../views/Home.vue';
import Message from '../views/Message.vue';
import Account from '../views/Account.vue';
import ApiConnect from '../views/ApiConnect.vue';
import Robot from '../views/Robot.vue';
import Payment from '../views/Payment.vue';
import Transaction from '../views/Transaction.vue';
import SettingTrading from '../views/SettingTrading.vue';
import BotTrading from '../views/BotTrading.vue';
import Login from '../views/Login.vue';
import Register from '../views/Register.vue';
import TickerSelect from '@/views/TickerSelect.vue';
import UserProfile from '@/views/UserProfilePage.vue';
import TransactionDetail from '@/views/TransactionDetail.vue';
import CopyConfig from '@/views/CopyConfig.vue';
import Layout from '@/views/layout.vue';
import Cookies from 'js-cookie';
import { APP_MODE } from '@/common/config';

Vue.use(Router);

class RouteMeta {
  title: string;

  constructor ({ title }: { title: string }) {
    this.title = title;
  }
}

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'app',
      redirect: 'home',
      component: Layout,
      meta: new RouteMeta({ title: 'Trade BOT 1.0' }),
      children: [
        {
          path: '',
          name: 'home',
          component: Home,
          meta: new RouteMeta({ title: 'Trade BOT 1.0' })
        },
        {
          path: 'message',
          name: 'message',
          component: Message,
          meta: new RouteMeta({ title: 'Tin nhắn - Thông báo' })
        },
        {
          path: 'account',
          name: 'account',
          component: Account,
          meta: new RouteMeta({ title: 'Tài khoản' })
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: new RouteMeta({ title: 'Đăng nhập Trade BOT 1.0' })
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: new RouteMeta({ title: 'Đăng ký tài khoản' })
    },
    {
      path: '/api-connect',
      name: 'api-connect',
      component: ApiConnect,
      meta: new RouteMeta({ title: 'Quản lý API' })
    },
    {
      path: '/robot',
      name: 'robot',
      component: Robot,
      meta: new RouteMeta({ title: 'Khởi động Robot' })
    },
    {
      path: '/payment',
      name: 'payment',
      component: Payment,
      meta: new RouteMeta({ title: 'Chi tiết thanh toán' })
    },
    {
      path: '/transaction',
      name: 'transaction',
      component: Transaction,
      meta: new RouteMeta({ title: 'Danh sách giao dịch' })
    },
    {
      path: '/transaction/detail',
      name: 'transactionDetail',
      meta: new RouteMeta({ title: 'Danh sách giao dịch' }),
      component: TransactionDetail
    },
    {
      path: '/setting-trading/:symbol',
      name: 'settingTrading',
      component: SettingTrading,
      meta: new RouteMeta({ title: 'Cài đặt giao dịch' })
    },
    {
      path: '/auto-trading/:symbol',
      name: 'autoTrading',
      component: BotTrading,
      meta: new RouteMeta({ title: 'Đặt lệnh' })
    },
    {
      path: '/account/copy-config',
      name: 'copyConfig',
      meta: new RouteMeta({ title: 'Copy cấu hình' }),
      component: CopyConfig
    },
    {
      path: '/user-profile',
      name: 'userProfile',
      meta: new RouteMeta({ title: 'Thông tin tài khoản' }),
      component: UserProfile
    },
    { path: '/:pathMatch(.*)*', name: 'NotFound', redirect: '/' }
  ],
  scrollBehavior (to, from, savedPosition) {
    return new Promise((resolve) => {
      setTimeout(() => {
        // @ts-ignore
        return resolve({ el: to.hash, behavior: 'smooth' });
      }, 500);
    });
  }
});

// This callback runs before every route change, including on initial load
router.beforeEach((to, from, next) => {
  const publicPages = ['/login', '/register'];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = Cookies.get('user');
  if (authRequired && !loggedIn) {
    next({ name: 'login' });
  } else {
    const routeMeta = to.meta as RouteMeta;
    let title = routeMeta.title;
    if (['autoTrading', 'settingTrading'].includes(to.name ?? '') && to.params.symbol) {
      title += ': ' + (to.params.symbol).replace('USDT', '/USDT');
    }
    store.dispatch('topToolbar/changeTitle', title);
    next();
  }
});

export default router;
