import axios from 'axios';
import Cookies from 'js-cookie';
import { API_URL, APP_MODE } from '@/common/config';
class AuthService {
  login (username: string, password: string) {
    return axios
      .post(API_URL + '/auth/signin', {
        username,
        password
      })
      .then(response => {
        if (response.data.accessToken) {
          Cookies.set('user', JSON.stringify(response.data), { expires: APP_MODE ? 365 : 1 });
        }

        return response.data;
      });
  }

  loginSocial (userInfo: any) {
    return axios
      .post(API_URL + '/auth/social-signin', userInfo)
      .then(response => {
        if (response.data.accessToken) {
          Cookies.set('user', JSON.stringify(response.data), { expires: 1 });
        }
        return response.data;
      });
  }

  logout () {
    Cookies.remove('user');
  }

  register (username: string, email: string, password: string) {
    return axios.post(API_URL + '/auth/signup', {
      username,
      email,
      password
    });
  }
}

export default new AuthService();
