import { VuexModule, Module, Mutation, Action } from 'vuex-module-decorators';
import AuthService from '@/services/authService';
import UserService from '@/services/userService';
import Cookies from 'js-cookie';

const storedUser = Cookies.get('user');

@Module({ namespaced: true })

class User extends VuexModule {
  public status = storedUser ? { loggedIn: true } : { loggedIn: false };
  public user = storedUser ? JSON.parse(storedUser) : null;
  public userInfo = null;

  @Mutation
  public loginSuccess (user: any): void {
    this.status.loggedIn = true;
    this.user = user;
  }

  @Mutation
  public setUserInfo (userInfo: any): void {
    this.userInfo = userInfo;
  }

  @Mutation
  public loginFailure (): void {
    this.status.loggedIn = false;
    this.user = null;
  }

  @Mutation
  public logout (): void {
    this.status.loggedIn = false;
    this.user = null;
    this.userInfo = null;
  }

  @Mutation
  public registerSuccess (): void {
    this.status.loggedIn = false;
  }

  @Mutation
  public registerFailure (): void {
    this.status.loggedIn = false;
  }

  @Action({ rawError: true })
  login (data: any): Promise<any> {
    return AuthService.login(data.username, data.password).then(
      (user: any) => {
        this.context.commit('loginSuccess', user);
        return Promise.resolve(user);
      },
      (error: { response: { data: { msg: any } }; msg: any; toString: () => any }) => {
        this.context.commit('loginFailure');
        const msg =
          (error.response && error.response.data && error.response.data.msg) ||
          error.msg ||
          error.toString();
        return Promise.reject(msg);
      }
    );
  }

  @Action({ rawError: true })
  loginSocial (data: any): Promise<any> {
    return AuthService.loginSocial(data).then(
      (user: any) => {
        this.context.commit('loginSuccess', user);
        return Promise.resolve(user);
      },
      (error: { response: { data: { msg: any } }; msg: any; toString: () => any }) => {
        this.context.commit('loginFailure');
        const msg =
          (error.response && error.response.data && error.response.data.msg) ||
          error.msg ||
          error.toString();
        return Promise.reject(msg);
      }
    );
  }

  @Action({ rawError: true })
  getUserInfo (): Promise<any> {
    return UserService.getUserInfo().then(
      (userInfo: any) => {
        this.context.commit('setUserInfo', userInfo);
        return Promise.resolve(userInfo);
      },
      (error: { response: { data: { msg: any } }; msg: any; toString: () => any }) => {
        this.context.commit('loginFailure');
        const msg =
          (error.response && error.response.data && error.response.data.msg) ||
          error.msg ||
          error.toString();
        return Promise.reject(msg);
      }
    );
  }

  @Action
  signOut (): void {
    AuthService.logout();
    this.context.commit('logout');
  }

  @Action({ rawError: true })
  register (data: any): Promise<any> {
    return AuthService.register(data.username, data.email, data.password).then(
      (response: { data: any }) => {
        this.context.commit('registerSuccess');
        return Promise.resolve(response.data);
      },
      (error: { response: { data: { msg: any } }; msg: any; toString: () => any }) => {
        this.context.commit('registerFailure');
        const msg =
          (error.response && error.response.data && error.response.data.msg) ||
          error.msg ||
          error.toString();
        return Promise.reject(msg);
      }
    );
  }

  get isLoggedIn (): boolean {
    return this.status.loggedIn;
  }

  get userData (): any {
    return this.userInfo;
  }
}

export default User;
