import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import vuetify from './plugins/vuetify';
import lang from './lang/lang';
import GAuth from 'vue-google-oauth2';
import VueI18n from 'vue-i18n';
import { gauthOption } from './common/config';
import { numberFormatter, toThousandFilter } from './filters';
import Router from 'vue-router';
import EasyRefresh from 'vue-easyrefresh';

import './styles/custom.css';

Vue.config.productionTip = false;
Vue.filter('numberFormatter', numberFormatter);
Vue.filter('toThousandFilter', toThousandFilter);

Vue.use(GAuth, gauthOption);
Vue.use(EasyRefresh);
Vue.use(VueI18n);

const i18n = lang.createI18n();
Router.prototype.back = function () {
  // @ts-ignore
  this.isBack = true;
  this.go(-1);
};

new Vue({
  router,
  i18n,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app');
