import axios from 'axios';
import authHeader from './authHeader';
import { API_URL } from '@/common/config';

class TradeDataService {
  fetch (params: any) {
    return axios
      .get(API_URL + '/trade-data', {
        headers: authHeader(),
        params: params
      })
      .then(response => {
        return response.data;
      });
  }

  listOrder (date: string) {
    return axios
      .get(API_URL + `/trade-data/detail/${date}`, {
        headers: authHeader()
      })
      .then(response => {
        return response.data;
      });
  }
}

export default new TradeDataService();
