




























































































































































































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator';
import TickerService from '@/services/tikerService';
import { Getter } from 'vuex-class';
import { SymbolTicker } from '@/types';
import { API_URL } from '@/common/config';
@Component({
  components: {

  }
})
export default class BotTrading extends Vue {
  @Getter('allTicker', { namespace: 'ticker' }) allTicker!: SymbolTicker[];
  private detailMsg = {
    type: '',
    msg: ''
  };

  private modeList = [
    { mode: 'onetime', name: 'Một lần' },
    { mode: 'cycle', name: 'Chu kỳ' },
    { mode: 'cycleAuto', name: 'Chu kỳ tự động' }
  ];

  apiUrl = API_URL;
  symbol = this.$route.params.symbol;
  loading = false;
  loadingOverlay = true;
  notConfig = true;
  sellDialog = false;
  dialogBuy = false;
  amountBuyMore = 0;
  model = {
    _id: '',
    firstPrice: 20,
    symbol: '',
    mode: 'cycle',
    modeSetting: 0,
    sellRate: 1.50,
    sellRateList: '',
    callbackBuy: 0.30,
    callbackSell: 0.30,
    numberOfOrder: 3,
    doubleReOrder: false,
    priceUpBuy: 0,
    orderTimeRate: '',
    orderTimeAmount: '',
    callbackSellList: '',
    callbackBuyList: '',
    tradeData: {
      buyList: [],
      totalAmount: 0,
      profit: 0,
      totalAmountTrade: 0
    },
    enable: false,
    pause: false
  };

  sellModel = {
    sellAll: false,
    amountUSDT: 0,
    amount: 0,
    lastPrice: 0,
    _id: ''
  };

  priceRule = [
    (v: number) => !!v || 'Vui lòng nhập số tiền'
  ];

  @Watch('symbolPrice')
  async changeSellAll (val: any) {
    (!this.model.tradeData.buyList && !this.notConfig) && await this.fetchConfig();
    if (this.sellModel.sellAll) {
      this.sellModel.amountUSDT = this.$options.filters && this.$options.filters.toThousandFilter(this.model.tradeData.totalAmountTrade * val);
    }
  }

  get symbolPrice () {
    const ticker = this.allTicker.find(x => x.s === this.model.symbol);
    return ticker ? ticker.c : 0;
  }

  get orderTimeRate () {
    return this.model.orderTimeRate ? JSON.parse(this.model.orderTimeRate) : [];
  }

  get orderTimeAmount () {
    return this.model.orderTimeAmount ? JSON.parse(this.model.orderTimeAmount) : [];
  }

  get sellRateList () {
    return this.model.sellRateList ? JSON.parse(this.model.sellRateList) : [];
  }

  get callbackSellList () {
    return this.model.callbackSellList ? JSON.parse(this.model.callbackSellList) : [];
  }

  get callbackBuyList () {
    return this.model.callbackBuyList ? JSON.parse(this.model.callbackBuyList) : [];
  }

  get sellPrice () {
    const buyTime = this.model.tradeData.buyList.length;
    const rate = this.model.modeSetting === 2 && buyTime > 1 ? (parseFloat(this.sellRateList[buyTime - 2]) + parseFloat(this.callbackSellList[buyTime - 2])) : (this.model.sellRate + this.model.callbackSell);
    return (this.model.tradeData.totalAmount / this.model.tradeData.totalAmountTrade) * ((rate) / 100 + 1);
  }

  get buyPrice () {
    const buyTime = this.model.tradeData.buyList ? this.model.tradeData.buyList.length : 0;
    const rate = this.model.modeSetting === 2 && buyTime > 0 ? this.orderTimeRate[buyTime - 1] : this.model.callbackBuy;
    return this.model.priceUpBuy <= 0 ? (this.model.tradeData.totalAmount / this.model.tradeData.totalAmountTrade * (1 - (rate / 100))) : this.model.priceUpBuy * ((rate / 100) + 1);
  }

  get amountSell () {
    return this.model.tradeData ? this.sellModel.amountUSDT / this.symbolPrice : 0;
  }

  async created () {
    await this.fetchConfig();
    this.loadingOverlay = false;
  }

  async fetchConfig () {
    await TickerService.fetch(this.$route.params.symbol).then(
      (res: any) => {
        if (res.success) {
          this.model = res.data;
          if (this.model.tradeData && this.model.tradeData.totalAmount) {
            this.model.tradeData.totalAmount = this.model.tradeData.totalAmount + (this.model.tradeData.profit ?? 0);
          }
          this.notConfig = false;
        }
      },
      () => {
        this.loadingOverlay = false;
      }
    );
  }

  sellAll () {
    this.sellModel.amountUSDT = this.getMaxAmount();
    this.sellModel.sellAll = true;
  }

  getMaxAmount () {
    return this.model.tradeData.totalAmountTrade * this.symbolPrice;
  }

  changeStatus () {
    this.loading = true;
    TickerService.changeStatus(!this.model.enable, this.model._id).then(
      async (res: any) => {
        if (res.success) {
          await this.fetchConfig();
          this.detailMsg = {
            type: 'success',
            msg: `Đã ${this.model.enable ? 'bật giao dịch' : 'ngừng giao dịch'}`
          };
          this.loading = false;
        }
      },
      (err) => {
        this.detailMsg = {
          type: 'error',
          msg: err.response.data.msg
        };
        this.loading = false;
      }
    );
  }

  pauseTrade () {
    this.loadingOverlay = true;
    TickerService.pauseTicker(!this.model.pause, this.model._id).then(
      async (res: any) => {
        if (res.success) {
          await this.fetchConfig();
          this.detailMsg = {
            type: 'success',
            msg: `Đã ${this.model.pause ? 'dừng lệnh mua' : 'khởi động lại giao dịch'}`
          };
          this.loadingOverlay = false;
        }
      },
      (err) => {
        this.detailMsg = {
          type: 'error',
          msg: err.response.data.msg
        };
        this.loadingOverlay = false;
      }
    );
  }

  async sellNow () {
    this.loadingOverlay = true;
    this.sellModel.amount = this.amountSell;
    this.sellModel.lastPrice = this.symbolPrice;
    this.sellModel._id = this.model._id;
    await TickerService.sellNow(this.sellModel).then(
      async (res: any) => {
        if (res.success) {
          await this.fetchConfig();
        }
      },
      (err) => {
        this.detailMsg = {
          type: 'error',
          msg: err.response.data.msg.name
        };
      }
    );
    this.loadingOverlay = false;
    this.sellDialog = false;
  }

  buyMore () {
    TickerService.buyMore(this.amountBuyMore, this.model._id).then(
      async (res: any) => {
        if (res.success) {
          await this.fetchConfig();
          this.loading = false;
        }
      }
    );
  }

  syncConfig () {
    this.loadingOverlay = true;
    TickerService.syncConfig(this.symbol).then(
      async (res: any) => {
        if (res.success) {
          this.detailMsg = {
            type: 'success',
            msg: 'Đồng bộ cấu hình thành công'
          };
        }
        this.loadingOverlay = false;
      },
      (err) => {
        this.detailMsg = {
          type: 'error',
          msg: err.response.data.msg.name
        };
        this.loadingOverlay = false;
      }
    );
  }
}
