


































import { Component, Vue } from 'vue-property-decorator';
import { Getter, namespace } from 'vuex-class';
import { UserInfo } from '@/types';
import TradeDataService from '@/services/tradeDataService';
import moment from 'moment';
const Auth = namespace('Auth');
@Component({
  components: {

  }
})
export default class TickerSelect extends Vue {
  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;
  tradingList = [];
  loading = true;
  date = this.$route.query.date;
  Page = 1;
  PageLimit = 9999;
  async created () {
    await this.getTradingList();
    this.loading = false;
  }

  async getTradingList () {
    // @ts-ignore
    this.$route.query.date && await TradeDataService.fetch({ sell: true, Page: this.Page, PageLimit: this.PageLimit, FromDate: this.date, ToDate: moment(this.date, 'YYYY-MM-DD').add(1, 'day').format('YYYY-MM-DD') }).then((res: any) => {
      if (res.success) {
        this.tradingList = res.data;
      }
    });
  }
}
