import axios from 'axios';
import authHeader from './authHeader';
import { API_URL } from '@/common/config';

class TickerService {
  fetch (symbol: string) {
    return axios
      .get(API_URL + `/ticker?symbol=${symbol}`, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  findTicker (params: any) {
    return axios
      .get(API_URL + '/find-ticker', { headers: authHeader(), params: params })
      .then(response => {
        return response.data;
      });
  }

  list () {
    return axios
      .get(API_URL + '/ticker/list', { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  listDetail (symbols: any) {
    return axios
      .get(API_URL + `/ticker/list-detail?symbols=${symbols}`, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  create (tickerConfig: any) {
    return axios
      .post(API_URL + '/ticker/create', tickerConfig, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  changeStatus (enable: boolean, _id: string) {
    return axios
      .post(API_URL + '/ticker/changeStatus', { enable: enable, _id: _id }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  pauseTicker (pause: boolean, _id: string) {
    return axios
      .post(API_URL + '/ticker/pause', { pause: pause, _id: _id }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  sellNow (sellData: any) {
    return axios
      .post(API_URL + '/ticker/sell-now', sellData, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  buyMore (amount: number, _id: string) {
    return axios
      .post(API_URL + '/ticker/buy-more', { amount: amount, _id: _id }, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  update (tickerConfig: any) {
    return axios
      .post(API_URL + '/ticker/update', tickerConfig, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  syncConfig (symbol: any) {
    return axios
      .get(API_URL + `/ticker/sync-config?symbol=${symbol}`, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }

  copyConfig (userId: any) {
    return axios
      .get(API_URL + `/ticker/copy-config?from=${userId}`, { headers: authHeader() })
      .then(response => {
        return response.data;
      });
  }
}

export default new TickerService();
