




























































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import TickerService from '@/services/tikerService';
import { Getter } from 'vuex-class';
import { SymbolTicker } from '@/types';

  @Component({
    components: {

    }
  })
export default class SettingTrading extends Vue {
    @Getter('allTicker', { namespace: 'ticker' }) allTicker!: SymbolTicker[];

    private detailMsg = {
      type: '',
      msg: ''
    };

    private modeList = [
      { mode: 'onetime', name: 'Một lần' },
      { mode: 'cycle', name: 'Chu kỳ' },
      { mode: 'cycleAuto', name: 'Chu kỳ tự động' }
    ];

    modeSetting = 0
    isUpdate = false;
    model = {
      firstTimeChange: 5,
      firstPrice: 20,
      minPriceUse: false,
      minPrice: 0,
      symbol: '',
      mode: 'cycle',
      sellRate: 0.80,
      callbackBuy: 0.30,
      callbackSell: 0.1,
      sellRateLastOrder: 0.8,
      callbackSellLastOrder: 0.1,
      numberOfOrder: 3,
      doubleReOrder: false,
      orderTimeRate: '',
      modeSetting: 0,
      orderTimeAmount: '',
      orderTimeChange: '',
      sellRateList: '',
      callbackBuyList: '',
      callbackSellList: '',
      downGuaranteed: false,
      downGuaranteedRate: 0,
      downGuaranteedTime: 1440
    };

    loading = false;
    loadingPage = true;
    orderTimeRate = [5, 10, 15, 20, 25];
    orderTimeAmount = [];
    orderTimeChange = [5, 5, 5, 5, 5];
    sellRateList: any[] = [];
    callbackBuyList: any[] = [];
    callbackSellList: any[] = [];
    orderTimeRateList = [
      {
        value: 1,
        text: '1 Lệnh'
      },
      {
        value: 2,
        text: '2 Lệnh'
      },
      {
        value: 3,
        text: '3 Lệnh'
      },
      {
        value: 4,
        text: '4 Lệnh'
      },
      {
        value: 5,
        text: '5 Lệnh'
      }
    ];

    priceRule = [
      (v: number) => !!v || 'Vui lòng nhập số tiền',
      (v: number) => (v && (v >= 10)) || 'Số tiền không được nhỏ hơn 10 USDT'
    ];

    numberRule = [
      (v: number) => !!(v.toString()) || 'Vui lòng nhập số'
    ];

    orderTimeRateRule = [(v: number) => !!v || 'Vui lòng chọn số lượng đặt hàng'];

    get symbolPrice () {
      const ticker = this.allTicker.find(x => x.s === this.model.symbol);
      return ticker ? ticker.c : 0;
    }

    async mounted () {
      await this.fetchConfig();
      this.loadingPage = false;
    }

    setModeSetting () {
      this.model.modeSetting = this.modeSetting;
    }

    async fetchConfig () {
      await TickerService.fetch(this.$route.params.symbol).then((res: any) => {
        if (res.success) {
          this.model = res.data;
          this.isUpdate = true;
          this.orderTimeRate = JSON.parse(this.model.orderTimeRate);
          this.model.modeSetting && (this.modeSetting = this.model.modeSetting);
          !this.model.firstTimeChange && (this.model.modeSetting = 5);
          !this.model.sellRateLastOrder && (this.model.sellRateLastOrder = this.model.sellRate);
          !this.model.callbackSellLastOrder && (this.model.callbackSellLastOrder = this.model.callbackSell);
          this.model.sellRateList && (this.sellRateList = JSON.parse(this.model.sellRateList));
          this.model.orderTimeAmount && (this.orderTimeAmount = JSON.parse(this.model.orderTimeAmount));
          this.model.orderTimeChange && (this.orderTimeChange = JSON.parse(this.model.orderTimeChange));
          this.model.callbackBuyList && (this.callbackBuyList = JSON.parse(this.model.callbackBuyList));
          this.model.callbackSellList && (this.callbackSellList = JSON.parse(this.model.callbackSellList));
          if (this.model.numberOfOrder > 1 && this.model.modeSetting === 2) {
            for (let i = 1; i <= this.model.numberOfOrder; i++) {
              if (!this.model.callbackSellList) {
                this.callbackSellList.push(this.model.callbackSell);
              }
              if (!this.model.callbackBuyList) {
                this.callbackBuyList.push(this.model.callbackBuy);
              }
              if (!this.model.sellRateList) {
                this.sellRateList.push(this.model.sellRate);
              }
            }
          }
        }
      },
      () => { console.log(''); });
    }

    async saveOrUpdate () {
      if (!(this.$refs.settingTradeFrm as Vue & { validate: () => boolean }).validate()) return;
      this.model.orderTimeRate = JSON.stringify(this.orderTimeRate.slice(0, this.model.numberOfOrder));
      this.model.orderTimeAmount = JSON.stringify(this.orderTimeAmount.slice(0, this.model.numberOfOrder));
      this.model.orderTimeChange = JSON.stringify(this.orderTimeChange.slice(0, this.model.numberOfOrder));
      this.model.sellRateList = JSON.stringify(this.sellRateList.slice(0, this.model.numberOfOrder));
      this.model.callbackBuyList = JSON.stringify(this.callbackBuyList.slice(0, this.model.numberOfOrder));
      this.model.callbackSellList = JSON.stringify(this.callbackSellList.slice(0, this.model.numberOfOrder));
      this.loading = true;
      if (!this.isUpdate) {
        this.model.symbol = this.$route.params.symbol;
        await TickerService.create(this.model).then(
          async () => {
            await this.fetchConfig();
            this.detailMsg = {
              type: 'success',
              msg: 'Cài đặt cặp lệnh thành công'
            };
          },
          () => {
            this.detailMsg = {
              type: 'error',
              msg: 'Cài đặt cặp lệnh thất bại'
            };
          }
        );
      } else {
        await TickerService.update(this.model).then(
          async () => {
            await this.fetchConfig();
            this.detailMsg = {
              type: 'success',
              msg: 'Cập nhật cặp lệnh thành công'
            };
          },
          () => {
            this.detailMsg = {
              type: 'error',
              msg: 'Cập nhật cặp lệnh thất bại'
            };
          }
        );
      }
      this.loading = false;
    }

    setFirstPrice () {
      if (this.model.minPriceUse) {
        this.model.minPrice = Number(this.symbolPrice);
      }
    }
}
