import { Getter, namespace } from 'vuex-class';
import { UserInfo } from '@/types';
import UserService from '@/services/userService';
const Auth = namespace('Auth');
@Component({
  components: {

  }
})
export default class TopStories extends Vue {
  @Getter('userData', { namespace: 'Auth' }) userData!: UserInfo;
  @Auth.Action
  private signOut!: () => Promise<any>;

  loadingOverlay = false;
  listConfig = ['autoResume', 'guaranteedProfit', 'profitRateAuto', 'pauseBot']

  async mounted () {
    /* if (this.userData && this.userData.autoResume) {
      this.settings.push(0);
    }
    if (this.userData && this.userData.guaranteedProfit) {
      this.settings.push(1);
    }
    if (this.userData && this.userData.profitRateAuto) {
      this.settings.push(2);
    }
    if (this.userData && this.userData.profitRateAuto) {
      this.settings.push(3);
    } */
  }

  logOut (username: string, password: string) {
    this.signOut().then(
      (data) => {
        window.location.href = '';
      }
    );
  }

  get settings () {
    const setting: string[] = [];
    this.listConfig.forEach(item => {
      // @ts-ignore
      if (this.userData && this.userData[item]) {
        setting.push(item);
      }
    });
    return setting;
  }

  set settings (val) {
    this.listConfig.forEach(item => {
      // @ts-ignore
      // eslint-disable-next-line eqeqeq
      if (this.userData[item] != val.includes(item)) {
        this.loadingOverlay = true;
        const param: never[] = [];
        // @ts-ignore
        this.userData[item] = param[item] = val.includes(item);
        console.log(param);
        const params = Object.assign({}, param);
        UserService.changeConfig(params).then(() => {
          this.loadingOverlay = false;
        });
      }
    });
    /* if (this.userData.guaranteedProfit !== val.includes(1)) {
      this.loadingOverlay = true;
      UserService.changeGuaranteedProfit(val.includes(1)).then((data) => {
        if (data.success) {
          this.userData.guaranteedProfit = val.includes(1);
        }
        this.loadingOverlay = false;
      });
    }
    if (this.userData.pauseBot !== val.includes(3)) {
      this.loadingOverlay = true;
      UserService.pauseBot(val.includes(3)).then((data) => {
        if (data.success) {
          this.userData.pauseBot = val.includes(3);
        }
        this.loadingOverlay = false;
      });
    } */
  }
}
